.modalWindow {
    position: fixed;
    top: 30%;
    left: 40%;
    z-index: 1000;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    border: 1px solid black;
    background-color: white;
    width: 400px;
    padding: 0 40px 40px;
    -webkit-animation: fadeIn .6s;
            animation: fadeIn .6s;
}

.restartWindow {
    opacity: 0;
    -webkit-animation: fadeOut .3s;
            animation: fadeOut .3s;
}

@-webkit-keyframes fadeOut {
    100% {
        opacity: 0;
        -webkit-transform: translate3d(100%, 0, 0);
                transform: translate3d(100%, 0, 0);
    }

    0% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    100% {
        opacity: 0;
        -webkit-transform: translate3d(100%, 0, 0);
                transform: translate3d(100%, 0, 0);
    }

    0% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, -100%, 0);
                transform: translate3d(0, -100%, 0);
    }

    100% {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, -100%, 0);
                transform: translate3d(0, -100%, 0);
    }

    100% {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

#modalWindow:hover {
    -webkit-box-shadow: 0 0 13px 0 rgba(0,0,0,0.75);
    box-shadow: 0 0 13px 0 rgba(0,0,0,0.75);
    -webkit-transition: ease .7s;
    -o-transition: ease .7s;
    transition: ease .7s;
    cursor: pointer;
}

#modalBtns {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-filter: blur(0);
            filter: blur(0);
}

#winnerTitle {
    text-align: center;
    margin-bottom: 50px;
    -webkit-filter: blur(0);
            filter: blur(0);
}

.modalBtn {
    background-color: black;
    color: white;
    font-size: 1rem;
    font-family: 'Rammetto One', cursive;
    border: 1px solid black;
    border-radius: 5%;
    padding: 10px 25px;
    cursor: pointer;
    outline: none;
    -webkit-filter: blur(0);
            filter: blur(0);
}

.modalBtn:hover {
    -webkit-box-shadow: 0 0 13px 0 rgba(0,0,0,0.75);
    box-shadow: 0 0 13px 0 rgba(0,0,0,0.75);
    -webkit-transition: ease .7s;
    -o-transition: ease .7s;
    transition: ease .7s;
    cursor: pointer;
}

.blur {
    -webkit-filter: blur(3px);
            filter: blur(3px);
}


